<template>
    <StepTemplate title=" 2.  ">
        <template v-slot:StepTemplate>
            <div class="flex">
                <div class="Step2__itemsBlock">
                    <el-switch
                            class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                            v-model="settings.header.useHeader"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text=" "
                            inactive-text=" "
                            :disabled="type !== previewType.application"
                    />
                    <div v-if="settings.header.useHeader">
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>  </span>
                            <el-color-picker
                                    v-model="settings.header.background"
                                    size="small"
                            />
                        </div>
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.isPercentHeight"
                                active-color="#13ce66"
                                inactive-color="#5252DD"
                                active-text="  "
                                inactive-text="  "
                        />
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>  {{ settings.header.isPercentHeight ? '(%)' : '(px)' }}</span>
                            <el-input-number
                                    v-model="settings.header.height"
                                    :min="settings.header.isPercentHeight ? 2 : 40"
                                    :max="settings.header.isPercentHeight ? 100 : 500"
                                    size="small"
                            />
                        </div>
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span></span>
                            <el-select
                                    size="small"
                                    v-model="settings.header.align"
                            >
                                <el-option
                                        v-for="item in sizeOptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                />
                            </el-select>
                        </div>


                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.hasLogo"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text=" "
                                inactive-text=" "
                        />
                        <div v-if="settings.header.hasLogo">
                            <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                <span>  </span>
                                <el-input
                                        placeholder=" "
                                        v-model="settings.header.logo.url"
                                        size="small"
                                        :clearable="true"
                                />
                            </div>
                            <el-switch
                                    class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                    v-model="settings.header.logo.auto"
                                    active-color="#13ce66"
                                    inactive-color="#ff4949"
                                    active-text=" "
                                    inactive-text=" "
                            />
                            <div v-if="!settings.header.logo.auto">
                                <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                    <span> </span>
                                    <el-input-number
                                            v-model="settings.header.logo.width"
                                            :min="20"
                                            size="small"
                                    />
                                </div>
                                <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                    <span> </span>
                                    <el-input-number
                                            v-model="settings.header.logo.height"
                                            :min="20"
                                            size="small"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="Step2__itemsBlock" v-if="settings.header.useHeader">
                    <el-switch
                            class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                            v-model="settings.header.hasTitle"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text=" "
                            inactive-text=" "
                    />
                    <div v-if="settings.header.hasTitle">
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span></span>
                            <el-input
                                    placeholder="    "
                                    v-model="settings.header.title.text"
                                    size="small"
                                    :clearable="true"
                            />
                        </div>
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span> </span>
                            <el-color-picker
                                    v-model="settings.header.title.color"
                                    size="small"
                            />
                        </div>
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>  </span>
                            <el-input-number
                                    v-model="settings.header.title.size"
                                    :min="0.5"
                                    :max="10"
                                    :step="0.1"
                                    size="small"
                            />
                        </div>
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.title.border"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text=" "
                                inactive-text=" "
                        />
                        <div v-if="settings.header.title.border"
                             class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span> </span>
                            <el-color-picker
                                    v-model="settings.header.title.borderColor"
                                    size="small"
                            />
                        </div>
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.title.twoLine"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text=". "
                                inactive-text=" "
                        />
                        <div v-if="settings.header.title.twoLine">
                            <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                <span></span>
                                <el-input
                                        placeholder="    "
                                        v-model="settings.header.title.twoLineText"
                                        size="small"
                                        :clearable="true"
                                />
                            </div>
                            <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                <span> </span>
                                <el-color-picker
                                        v-model="settings.header.title.twoLineColor"
                                        size="small"
                                />
                            </div>
                            <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                <span>  </span>
                                <el-input-number
                                        v-model="settings.header.title.twoLineSize"
                                        :min="0.5"
                                        :max="10"
                                        :step="0.1"
                                        size="small"
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div class="Step2__itemsBlock" v-if="settings.header.useHeader">
                    <el-switch
