                            <el-input-number
                                    v-model="settings.header.contacts.size"
                                    :min="0.5"
                                    :max="10"
                                    :step="0.1"
                                    size="small"
                            />
                        </div>
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.contacts.hasPhone"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text=" "
                                inactive-text=" "
                        />
                        <div v-if="settings.header.contacts.hasPhone" class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span></span>
                            <el-input
                                    placeholder=" "
                                    v-model="settings.header.contacts.phone"
                                    size="small"
                                    :clearable="true"
                            />
                        </div>
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.contacts.hasEmail"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text=" "
                                inactive-text=" "
                        />
                        <div v-if="settings.header.contacts.hasEmail" class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span></span>
                            <el-input
                                    placeholder=""
                                    v-model="settings.header.contacts.email"
                                    size="small"
                                    :clearable="true"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </StepTemplate>
</template>

<script>
    import StepTemplate from "@/components/SwitchableArea/StepTemplate/StepTemplate";
    import {previewType} from "@/constants";

    export default {
        name: "Step2",
        props: {
            type: {required: true, type: Number},
            settings: {required: true, type: Object},
        },
        components: {StepTemplate},
        data() {
            return {
                previewType: previewType,
                sizeOptions: [
                    {
                        value: 'size',
                        label: ' '
                    },
                    {
                        value: 'around',
                        label: '   '
                    },
                    {
                        value: 'center',
                        label: ' '
                    }
                ]
            }
        }
    }
</script>

<style src="./style.css"/>